//首頁
.header__bottom {
    margin-top: -77px;
    position: relative;
    &__txtBox {
        padding: 236px 0;
        .pageTitle {
            font-size: 48px;
            font-weight: $fw-bd;
        }
    }
    &__bg {
        top: 0;
        bottom: 0;
        right: 0;
        position: absolute;
        background-image: url('https://images.unsplash.com/photo-1557644978-f61037cfbe49?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80');
    }
    .c-btn {
        width: 40%;
    }
    @include pad-width {
        &__txtBox {
            padding-top: 188px;
            padding-bottom: 24px;
        }
        .pageTitle {
            color: white;
            text-align: center;
        }
        h5 {
            color: $light;
            text-align: center;
            margin-bottom: 108px;
        }
        .c-btn {
            width: 100%;
        }
    }
}

// 產品頁面
.brandDirector {
    background-image: url('https://images.unsplash.com/photo-1579825317824-2cc3aa33f75a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=634&q=80');
    .section--padding {
        padding: 150px 0;
    }
    .section__titleBox {
        h3 {
            font-size: 32px;
            font-weight: $fw-bd;
            line-height: 1.5;
        }
        @include pad-width {
            margin-bottom: 125px;
        }
    }
    .section__txtBox {
        p {
            font-weight: $fw-sm;
        }
        h5 {
            font-size: 24px;
        }
        a {
            font-weight: $fw-bd;
        }
    }
}

.brandConcept {
    .brandConcept__txtBox {
        height: 400px;
    }
    .bg-size {
        @include pad-width {
            height: 255px;
        }
    }
}

.product__list {
    background-color: $gray-300;
    .product__list__items {
        position: relative;
        font-size: 24px;
    }
    //這裡有點土砲寫法，感覺是不正確的。
    .product__list__amount {
        margin-top: 4px;
        font-size: 6px;
    }
}
//個別產品頁面
.productDetail__box {
    .productDetail__picBox {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
    }
    .productDetail__box__txtbox {
        padding-top: 64px;
        padding-bottom: 64px;
        .card {
            width: 100%;
            h3 {
                font-size: 48px;
            }
            .card__price {
                text-align: right;
                color: $light;
            }
            span {
                color: $dark;
                font-size: 24px;
            }
            .card__btnBox {
                height: 56px;
            }
        }
    }
    .productDetail__concept {
        font-size: 20px;
    }
}
.card.cmb-6 {
    margin-bottom: 48px;
}
